$customYellow: #cca767;
$customGray: #818180;
$customWhite: rgb(255, 255, 255);
$customBlack: rgb(0, 0, 0);

.loading {
  position: relative;
  min-height: 100vh;
  position: fixed;
  z-index: 99999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 1);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &>svg{
    fill: $customYellow;
    animation-duration: 1s;
  }
}
