$customYellow: #cca767;
$customGray: #818180;
$customWhite: rgb(255, 255, 255);
$customBlack: rgb(0, 0, 0);

.about__head {
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;

  & > .about__base {
    & > h1 {
      color: $customYellow;
      text-transform: uppercase;
      font-size: 2em;
      letter-spacing: 5px;
    }
  }
}

@media (max-width: 1000px) {
    .about__head {
      height: 400px;
    }
  }

