//   align-items: center;
// }
.about_list {
  min-height: 100vh;
  max-width: 1000px;
  margin: 0 auto;
  & > h1 {
    text-align: center;
    margin: 20px auto;
    width: fit-content;
    border-bottom: 1px solid $customYellow;

  }
}

//
// }

.men {
  margin: 80px 0;
  & > .men_title {
    margin: 20px 20px;
    position: relative;
    &::before {
      content: "";
      border: 1px solid $customYellow;
      position: absolute;
      bottom: 0;

      width: 50%;
    }
  }
  & > .men_desc {
    & > .men_text {
        float: right;
        margin: 0 0 80px 0;
      & > .men_photo {
        width: 40%;
        max-width: 200px;
        margin: 10px 10px 0 10px;
        float: left;
      }
    }
  }
}


